/* Modern light theme with soft green palette */
:root {
  --primary-color: #4caf50;
  --primary-light: #a5d6a7;
  --primary-lighter: #e8f5e9;
  --text-dark: #2e7d32;
  --text-light: #f1f8e9;
  --background: #ffffff;
  --shadow: 0 2px 10px rgba(0, 0, 0, 0.08);
  --border-radius: 12px;
  --transition: all 0.3s ease;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', 'Segoe UI', 'Helvetica Neue', sans-serif;
  background-color: var(--primary-lighter);
  color: #333;
  padding: 20px;
  line-height: 1.6;
}

.app {
  max-width: 800px;
  margin: 0 auto;
  background-color: var(--background);
  border-radius: var(--border-radius);
  overflow: hidden;
  box-shadow: var(--shadow);
}

header {
  background-color: var(--primary-color);
  color: var(--text-light);
  padding: 25px 20px;
  text-align: center;
}

header h1 {
  font-size: 28px;
  font-weight: 600;
  margin-bottom: 5px;
}

.date {
  font-size: 14px;
  opacity: 0.9;
}

.author {
  font-size: 12px;
  opacity: 0.8;
  margin-top: 5px;
  font-style: italic;
}

.progress-container {
  padding: 20px;
  background-color: var(--primary-lighter);
}

.progress-text {
  text-align: center;
  color: var(--text-dark);
  font-weight: 600;
  margin-bottom: 10px;
  font-size: 18px;
}

.progress-bar {
  height: 12px;
  background-color: #e0e0e0;
  border-radius: 10px;
  overflow: hidden;
}

.progress-fill {
  height: 100%;
  background-color: var(--primary-color);
  border-radius: 10px;
  transition: width 0.3s ease;
}

.habits-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(230px, 1fr));
  gap: 15px;
  padding: 20px;
}

.habit-card {
  background-color: var(--background);
  border: 1px solid #e0e0e0;
  border-radius: var(--border-radius);
  padding: 15px;
  text-align: center;
  cursor: pointer;
  transition: var(--transition);
  position: relative;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.habit-card:hover {
  transform: translateY(-3px);
  box-shadow: var(--shadow);
  border-color: var(--primary-light);
}

.habit-card.completed {
  background-color: var(--primary-lighter);
  border-color: var(--primary-color);
}

.habit-name {
  font-weight: 500;
  color: #555;
}

.completed .habit-name {
  color: var(--text-dark);
}

.check-mark {
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: var(--primary-color);
  color: white;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
}

/* Media queries for responsiveness */
@media (max-width: 768px) {
  .habits-grid {
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    gap: 12px;
  }
  
  .habit-card {
    height: 90px;
  }
  
  .app {
    max-width: 95%;
  }
  
  body {
    padding: 15px;
  }
}

@media (max-width: 480px) {
  .habits-grid {
    grid-template-columns: 1fr 1fr;
    gap: 10px;
  }
  
  .habit-card {
    height: 80px;
    padding: 10px;
  }
  
  header h1 {
    font-size: 22px;
  }
  
  .habit-name {
    font-size: 13px;
  }
  
  .progress-text {
    font-size: 16px;
  }
  
  body {
    padding: 10px;
  }
  
  .app {
    max-width: 100%;
  }
}

/* For very small screens */
@media (max-width: 350px) {
  .habits-grid {
    grid-template-columns: 1fr;
  }
  
  header h1 {
    font-size: 20px;
  }
  
  .progress-container {
    padding: 15px 10px;
  }
}
